@import "../styles/left";
@import "../styles/overview";

.profile{
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--first-color);
    width: 100%;
    nav{
        margin: 30px 0 0 100px;
        display: flex;
        gap: 20px;

        .profileIcons{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 8px;
            border-radius: 4px;
            cursor: pointer;

            &:hover{
                background-color: var(--btn-color);
            }


            h2{
                font-size: 15px;
                margin-left: 4px;
                transition-delay: 0.04s;

            }
        }


    }

    .lineFour{
        @include lines();
    }
    .sectionCenter{
        display: flex;
        gap: 30px;
    }
}